export class Timer {
    static throttle(func, delay) {
        let isThrottled = false;
        let savedArg = null;
        // eslint-disable-next-line @typescript-eslint/no-this-alias
        let savedThis = this;
        function wrapper(e) {
            if (isThrottled) {
                savedArg = e;
                savedThis = this;
                return;
            }
            func.apply(this, [e]);
            isThrottled = true;
            setTimeout(() => {
                isThrottled = false;
                if (savedArg) {
                    wrapper.apply(savedThis, [savedArg]);
                    savedArg = null;
                }
            }, delay);
        }
        return wrapper;
    }
}
