import { KeyCode, KeyUtils } from "@devexpress/utils/lib/utils/key";
import { SchedulerKbdStrategy } from "./scheduler-kbd-strategy";
export class SchedulerToolbarContainerKbdStrategy extends SchedulerKbdStrategy {
    constructor(scheduler, targetElement, rootStrategy) {
        super(scheduler, targetElement, true);
        this.rootStrategy = rootStrategy;
    }
    leaveTransitContainer(direction) {
        super.leaveTransitContainer(direction);
        this.rootStrategy.moveFromToolbar(direction);
    }
    handleKeyDown(evt) {
        const keyCode = KeyUtils.getEventKeyCode(evt);
        if (keyCode === KeyCode.Key_n) {
            this.performShortcutEvent(evt);
            return true;
        }
        return false;
    }
}
