import ParsedAppointmentClientKey from "../types/parsed-appointment-client-key";
import SchedulerDomUtils from "../utils/scheduler-dom-utils";
import { DomUtils } from "@devexpress/utils/lib/utils/dom";
import SchedulerCssClasses from "../constants/scheduler-css-classes";
export default class AppointmentUtils {
    static selectSameAppointments(parsedKey, appointments) {
        appointments.filter(apt => {
            const aptKey = SchedulerDomUtils.Attr.getAppointmentKey(apt);
            const aptParsedKey = ParsedAppointmentClientKey.from(aptKey); // eslint-disable-line new-cap
            return ParsedAppointmentClientKey.IsSame(parsedKey, aptParsedKey, true); // eslint-disable-line new-cap
        }).forEach(apt => {
            if (!DomUtils.hasClassName(apt, SchedulerCssClasses.SelectedAppointmentClassName))
                DomUtils.addClassName(apt, SchedulerCssClasses.SelectedAppointmentClassName);
        });
    }
}
